@media (prefers-reduced-motion: no-preference) {
  .App-logo {
//     animation: App-logo-spin infinite 20s linear;
  }
}

.modal__body--wrapper > div {
    margin-left: auto !important;
    margin-right: auto !important;
}


svg {
    display: block;
    margin: 0 auto;
}

.axis .domain {
    display: none;
}

.axis--x text {
    fill: #999;
}

.axis--x line {
    stroke: #aaa;
}

.axis--activity .tick line {
    display: none;
}

.axis--activity text {
    font-size: 12px;
    fill: #777;
}

.axis--activity .tick:nth-child(odd) text {
    fill: #222;
}

.line {
    fill: none;
    stroke: #fff;
}

.area {
    fill: #448cab;
}

.activity:nth-child(odd) .area {
    fill: #5ca3c1;
}


svg path:hover {
    cursor: pointer;
}

svg {
transition: all .5s ease-out;

    rect {

transition: all .5s ease-out;
}

path {

transition: all .5s ease-out;
}
}

.legendWrapper {
    height: 280px;
}

.App-link {
  color: #61dafb;
}

