#modalQuicklinks {
    transition: all 2s;
}
.App-header {
    background-color: #fff;
    min-height: 100vh;
    padding-top: 76px;
    padding-left: 2em;
    padding-right: 2em;
    display: flex;
    flex-direction: column;
    align-items: start;
    /* justify-content: center; */
    font-size: calc(10px + 2vmin);
    color: #4e4e4e;
}
#modalQuicklinks .cds--modal.is-visible {
    transition: all 2s;
    top: 0;
    left: 200px;    border-left: 1px solid #d7346c;
    width: calc(100% - 200px);
    max-height: calc(100% - 0px);
    height: 100%;
    opacity: .95;
}

#modalQuicklinks.cds--nav-expanded .cds--modal.is-visible {
    transition: all 2s;
    left: 256px;
    width: calc(100% - 256px);
    max-height: calc(100%);
}

#modalQuicklinks .cds--modal-container {
    transition: all 2s;
    top: 0;
    left: 200px;
    width: 100%;
    max-height: 100%;
    height: 100%;
    opacity: .95;
}

#modalLeaf #guidanceModal .cds--modal-header {
    display: initial;
}

#modalLeaf #guidanceModal .cds--modal-container {
    background: rgb(238, 238, 238);

}

#modalLeaf #guidanceModal.cds--modal.is-visible {
    border: 1px solid var(--cds-link-primary, #d31e5d);
    position: absolute;
    height: inherit;
    top: 0;
    marginLeft: 15px;

    left: 15px;
    right: 15px;

    width: calc(100% - 30px);

}

#modalLeaf .cds--modal.is-visible {
    transition: opacity 2s;
    top: 0px;
    left: 200px;
    width: calc(100% - 200px);
//     max-height: calc(100% - 48px);
    height: 100%;
    opacity: 1;
}

#modalLeaf.cds--nav-expanded .cds--modal.is-visible {
    transition: opacity 2s;
    background: white;
    left: 256px;
    width: calc(100% - 256px);
//     max-height: calc(100% - 48px);
}

#modalLeaf .cds--modal-content {
    padding: 0;
    margin-bottom: 0;
}

.cds--side-nav__items {
    padding-top: 0;
    margin-top: 0;
}
#modalLeaf .leaf__mainContent {
    padding-left: 2rem;
    padding-top: 1rem;
    text-align: left;
}
#revEstimateWrapper {
    display: block;
}
