.landing-page {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

@media screen and (min-width: 768px) {
  .landing-page {
    flex-direction: row;
  }
}
.landing-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    max-height: 768px;
}

@media screen and (max-width: 768px) {
  .landing-page {
    max-height: none;
    display: block;
    margin-top: 12em;
  }
}

.landing-page__section {
  width: 100%;
  padding: 4rem;
  text-align: center;
}

.landing-page__section--break {
    flex-basis: 100%;
    width: 0;
    height: auto;
}

@media screen and (min-width: 768px) {
  .landing-page__section {
    width: 50%;
    text-align: left;
  }

   .landing-page__section.landing-page__section--0 {
    width: 100%;
   }
}

#sampleSize {
    color: #0f62fe;
    font-size: 18px;
    text-align: center;
}



.landing-page__section--1 {
  background-color: #393939;
  color: #fff;
}

.landing-page__section--2 {
  background-color: #f3f3f3;
}

.landing-page__divider {
  width: 100%;
  height: 2px;
  background-color: #ddd;
  margin: 4rem 0;
}

@media screen and (min-width: 768px) {
  .landing-page__divider {
    display: none;
  }
}

.landing-page__main-title {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.landing-page__title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.landing-page__description {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.landing-page__button {
  font-size: 1.25rem;
  margin-top: 2rem;
}
